import React from 'react';
import {projects} from '../data/projects';

const Project = () => {
  const mapProject = projects.map((project) => (
    <section className="project">
      <div className="flex-row flex-space-between">
        <h1>
          {project.name}
          <a href={project.github} rel="noopener noreferrer" target="_blank"><span>&nbsp;</span>
            <i className="fa fa-github"></i>
          </a>
        </h1>
      </div>
      <h6>{project.description}</h6>
      <p style={{fontStyle:'italic'}}>stack: {project.stack}</p>
    </section>
  ));

  return (
    <div className="block">
      <h1 className="section-header">Open Source Projects</h1>
      {mapProject}
    </div>
  );
};

export default Project;
