import React from 'react';
import { honors } from '../data/honors';

const Honor = () => {
  const mapHonors = honors.map((honor) => (
    <div>
      <div class="flex-row flex-space-between flex-nowrap">
        <h6>
          <strong>{honor.title}</strong>
        </h6>

        <span class="date">{honor.date}</span>
      </div>

      <span>{honor.description}</span>
    </div>
  ));
  return (
    <div className="block">
      <h1 className="section-header">
        Honors<span>&#38;</span>Awards
      </h1>
      <section className="honors">
        {mapHonors}
      </section>
    </div>
  );
};

export default Honor;
